body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center; }

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 15px;
  line-height: 25px; }

th {
  background-color: #eee; }

td,
th {
  text-align: center; }

td:first-child {
  text-align: left; }

input {
  min-width: 300px;
  border: 1px solid #999;
  border-radius: 2px;
  line-height: 25px; }

#main {
  display: flex;
  justify-content: flex-start; }
  #main #merge {
    background: rgba(0, 0, 0, 0.1);
    width: 100%; }
  #main .box {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 10px; }
    #main .box .btns {
      display: flex;
      justify-content: flex-start;
      align-items: center; }
    #main .box .btn {
      margin-left: 10px; }
    #main .box div {
      margin-left: 10px; }
  #main .option {
    display: flex;
    justify-content: flex-start;
    margin-top: 10px; }
    #main .option .option-li {
      border: 1px solid #eee;
      padding: 20px;
      margin-left: 10px; }
    #main .option .row {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      margin-top: 10px; }
      #main .option .row .h3 {
        color: #333;
        font-size: 16px;
        width: 100px;
        margin-right: 10px; }
      #main .option .row .ant-btn {
        margin-right: 10px; }
      #main .option .row .ant-input {
        width: 150px;
        min-width: auto; }

